        span 2
      h1 La gerencia de la información y el conocimiento en la toma de decisiones
    
    .row.mb-5(data-aos='fade')
      .col-lg-5.pe-lg-0.d-none.d-lg-block
        .bg1.d-flex.align-items-center.h-100
