      .col-lg-7.ps-lg-0
        .bg1.p-4.py-lg-3.h-100.d-flex.align-items-center.justify-content-center.flex-column
          p En el entorno empresarial actual, las organizaciones están cada vez más interesadas en identificar y optimizar el uso del conocimiento que poseen. El conocimiento se ha convertido en uno de los activos económicos más valiosos dentro de una empresa. Por lo tanto, es fundamental establecer estrategias para adquirir, retener, administrar y utilizar este recurso de manera efectiva.
          p.mb-0 La gerencia del conocimiento abarca todo lo que la empresa sabe acerca de sus productos, el mercado, los clientes, el equipo de trabajo, y cómo integrar estos elementos para ser competitiva en la economía moderna. Esta gestión se transforma en un proceso dinámico y cultural que permite alinear el uso del conocimiento con los objetivos de la organización. 
          p.mb-0 Como resultado, la empresa se convierte en una organización que aprende y se adapta continuamente.

    .row.align-items-center.subt.mb-4(data-aos="fade-left")
      .col-2.col-md-auto
        img(src='@/assets/curso/temas/t4/subt.svg', alt='')
      .col-10.col-md-auto.ps-0
        h3.mb-0 Componentes clave de la gerencia del conocimiento

    p.mb-4 Dentro de los principales componentes que abarca la gerencia del conocimiento, se identifican los siguientes:
    
    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-10
        .tarjeta.bgimg1.p-4
