              .col-md-8.col-lg-7
                h4 1. Captura y almacenamiento de información
                p Es fundamental recolectar información que no está fácilmente accesible, transformarla en conocimiento útil, y luego distribuirla a través de medios digitales, haciéndola accesible a todos los niveles de la organización.
              .col-md-4
                img(src='@/assets/curso/temas/t2/img2.png', alt='')
            .row.align-items-center.justify-content-evenly.py-lg-4(titulo="")
              .col-md-8.col-lg-7
                h4 2. Transformación de la información en conocimiento
                p Este proceso debe ser continuo y garantizar que el conocimiento sea aplicable y relevante para la toma de decisiones.
              .col-md-4
                img(src='@/assets/curso/temas/t2/img3.png', alt='')
            .row.align-items-center.justify-content-evenly.py-lg-4(titulo="")
              .col-md-8.col-lg-7
                h4 3. Difusión eficiente del conocimiento
                p La distribución debe facilitar la conexión entre la información y las personas que la necesitan, creando un flujo de conocimiento entre los empleados y los datos. 
              .col-md-4
                img(src='@/assets/curso/temas/t2/img4.png', alt='')
    
    p Un factor distintivo en la gestión del conocimiento es el enfoque en las conexiones entre las personas y la información. Más que la cantidad de datos, lo que realmente importa es cómo se conectan y utilizan esos datos para crear valor. Este aspecto diferenciador marca la diferencia entre la gerencia del conocimiento y la gerencia de la información.
    p.mb-4 Beneficios de la gestión del conocimiento:

    TabsB.color-acento-contenido.mb-5(data-aos="fade-down")
      .py-4.py-md-5(titulo="Competitividad" :icono="require('@/assets/curso/temas/t2/tab1.svg')")
