            p Aprovechar el conocimiento permite a las empresas mejorar su capacidad para enfrentar tanto problemas simples como complejos, incrementando su competitividad.       
          .col-md-4.col-lg-2.col-7
            img(src='@/assets/curso/temas/t2/img5.svg', alt='')      
      .py-4.py-md-5(titulo="Rentabilidad" :icono="require('@/assets/curso/temas/t2/tab2.svg')")
        .row.justify-content-evenly
          .col-md-8
            h4 Rentabilidad
            p Al compartir y aplicar el conocimiento, las organizaciones aumentan su productividad y, en consecuencia, su rentabilidad.     
          .col-md-4.col-lg-2.col-7
            img(src='@/assets/curso/temas/t2/img6.svg', alt='')      
      .py-4.py-md-5(titulo="Eficiencia operativa" :icono="require('@/assets/curso/temas/t2/tab3.svg')")
        .row.justify-content-evenly
          .col-md-8
            h4 Eficiencia operativa
            p Las empresas que gestionan bien su conocimiento evitan la duplicación de tareas y minimizan el tiempo perdido, mejorando la eficiencia general.    
          .col-md-4.col-lg-2.col-7
            img(src='@/assets/curso/temas/t2/img7.svg', alt='')
    
    p Las empresas que se enfocan en aprovechar al máximo su conocimiento se posicionan mejor en el mercado. No solo están preparadas para compartir su saber, sino que también evolucionan y se adaptan más rápidamente que sus competidores. Este proceso de aprendizaje compartido no solo se basa en las experiencias internas, sino también en las lecciones aprendidas de otras empresas.
    p.mb-4 Dentro de los objetivos de la gestión del conocimiento se destacan los siguientes:

    .titulo-sexto.color-acento-contenido(data-aos="fade-down")
      h5 Figura 3.
      span Objetivos de la gestión del conocimiento
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-6.col-md-10
        img(src='@/assets/curso/temas/t2/fig3.svg', alt='La figura presenta en forma de diagrama los objetivos de la gestión del conocimiento que son: incrementar las oportunidades de negocio, mejorar la comunicación interna, aumentar la competitividad actual y futura, fortalecer el liderazgo en el mercado y optimizar el rendimiento empresarial.')
    
    p.mb-4 El propósito principal de la gestión del conocimiento es traducir el conocimiento en acción, lo cual, a su vez, se traduce en resultados concretos. En el contexto actual, las empresas dependen cada vez más del conocimiento por diversas razones:

    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-3.col-md-6.mb-3.mb-lg-0
        .tarjeta.tarj-slider.p-4.py-5
          .align-items-center
            .row.justify-content-center.my-3
              .col-4
                img(src='@/assets/curso/temas/t2/ico1.svg' alt='')
            h4.text-center.mb-3 Maximización de aptitudes
            p.text-center Aprovechar las capacidades de los empleados incrementa la productividad y asegura la supervivencia en un entorno competitivo.
      .col-lg-3.col-md-6.mb-3.mb-lg-0
        .tarjeta.tarj-slider.p-4.py-5
          .align-items-center
            .row.justify-content-center.my-3
              .col-4
                img(src='@/assets/curso/temas/t2/ico2.svg' alt='')
            h4.text-center.mb-3 Ciclo de vida del conocimiento
            p.text-center El conocimiento se vuelve obsoleto más rápidamente, lo que exige una constante actualización.
      .col-lg-3.col-md-6.mb-3.mb-lg-0
        .tarjeta.tarj-slider.p-4.py-5
          .align-items-center
            .row.justify-content-center.my-3
              .col-4
                img(src='@/assets/curso/temas/t2/ico3.svg' alt='')
            h4.text-center.mb-3 Avances tecnológicos
            p.text-center Las telecomunicaciones e Internet han reducido las barreras de tiempo y espacio, facilitando el acceso y el intercambio de información.
      .col-lg-3.col-md-6.mb-3.mb-lg-0
        .tarjeta.tarj-slider.p-4.py-5
          .align-items-center
            .row.justify-content-center.my-3
              .col-4
                img(src='@/assets/curso/temas/t2/ico4.svg' alt='')
