            p.text-center Los bienes y servicios contienen cada vez más conocimiento, tanto en términos de calidad como de cantidad. 

    .cajon.color-acento-contenido.bg2.p-4(data-aos="fade-down")
      p.mb-0 Las organizaciones del futuro se destacarán por su cultura organizacional y su capacidad para explotar el conocimiento acumulado, ya sea para innovar o resolver problemas de manera rápida y eficiente. Estas empresas, conocidas como organizaciones inteligentes, serán expertas en crear y adquirir conocimiento, así como en modificar su comportamiento para reflejar nuevos aprendizajes. 
    

    Separador
    #t_2_1.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 2.1  La gerencia del conocimiento
    
    .cajon.color-acento-contenido.bg2.p-4.mb-4(data-aos="fade-down")
      p.mb-0 La gerencia del conocimiento se define como el aprovechamiento estratégico del saber que reside en el talento humano de una organización. Su objetivo principal es aumentar la capacidad de respuesta y fomentar la innovación. En un entorno competitivo, donde los diferenciadores entre empresas son cada vez más escasos, gestionar eficazmente el conocimiento se ha convertido en un factor clave para movilizar los recursos intelectuales y asegurar el éxito de la organización.
     
    p.mb-4 En la sociedad de la información, el conocimiento ha pasado a ser el principal factor de producción. Sin embargo, la gestión de este activo presenta una dificultad importante: reside en la compleja mente de las personas, lo que hace que su manejo requiera habilidades específicas. Para ser eficaz, la gerencia del conocimiento debe:

    .row.mb-4(data-aos="fade")
      .col-lg-7
        ol.lista-ol--cuadro.lista-ol--separador.fa-ul
          li 
            .lista-ol--cuadro__vineta
              span.text-dark.fw-bold 1
            span.mb-0 #[b Identificar, clasificar, medir y valorar el capital intelectual:] esto implica canalizar las habilidades, experiencias y capacidades del personal, convirtiéndolas en activos estructurales que generen valor para la empresa.
          li 
            .lista-ol--cuadro__vineta
              span.text-dark.fw-bold 2
            span.mb-0 #[b Implementar mejores métodos de trabajo:] crear una estructura organizacional dinámica y eficiente que favorezca una relación cercana con el entorno y la integración de las tecnologías de la información.
          li 
            .lista-ol--cuadro__vineta
              span.text-dark.fw-bold 3
            span.mb-0 #[b Detectar proveedores de conocimiento:] identificar a los individuos que poseen conocimiento valioso y facilitar su transferencia dentro de la organización.
    
    p.mb-4 Las organizaciones manejan dos tipos principales de conocimiento:

    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-4.col-md-6.mb-3.mb-md-0
        .row.px-2.pt-2.h-100
          .col-12.m-0.nav-holder2.align-items-center.px-0
            img(src="@/assets/curso/temas/t2/tarj1-1.png", alt="")
            .text.p-2
              .row.text-center.px-1.pt-1.mt-md-4.pt-md-0.mt-lg-3.mt-xl-4
                h4.mt-3.mb-3 Conocimiento explícito
                p.px-4 Es aquel que puede codificarse y transmitirse fácilmente a otras personas, como manuales, procedimientos o bases de datos.
      .col-lg-4.col-md-6.mb-3.mb-md-0
        .row.px-2.pt-2.h-100
          .col-12.m-0.nav-holder2.align-items-center.px-0
            img(src="@/assets/curso/temas/t2/tarj1-2.png", alt="")
            .text.p-2
              .row.text-center.px-1.pt-1.mt-md-4.pt-md-0.mt-lg-3.mt-xl-4
                h4.mt-3.mb-3 Conocimiento tácito
                p.px-4 Este tipo de conocimiento está basado en la experiencia y en el instinto, lo que lo hace difícil de formalizar. Suele estar ligado a criterios personales y contextuales.

    .row.align-items-center.subt.mb-4(data-aos="fade-left")
      .col-2.col-md-auto
        img(src='@/assets/curso/temas/t4/subt.svg', alt='')
      .col-10.col-md-auto.ps-0
        h3.mb-0 Gerencia del conocimiento en las pymes

    p La gerencia del conocimiento es vista como una técnica gerencial clave para las pymes, que deben enfrentar los mismos desafíos que las grandes empresas, pero con menos recursos. Hoy en día, el conocimiento se ha convertido en una fuente importante de ingresos, lo que obliga a las pequeñas y medianas empresas a adoptar técnicas efectivas para gestionar este recurso.
    p.mb-4 Una solución viable es crear una unidad de gerencia del conocimiento dentro de las pymes. Esta unidad tiene como objetivo actuar como un observatorio del saber, alineado con los objetivos de la empresa. Además, debe participar activamente en la formación y entrenamiento del personal.
    
    ol.lista-ol--cuadro.fa-ul.mb-4(data-aos="fade")
      li 
        .lista-ol--cuadro__vineta
          span.text-dark.fw-bold 1
        | Formular estrategias que desarrollen y apliquen el conocimiento organizacional.
      li 
        .lista-ol--cuadro__vineta
          span.text-dark.fw-bold 2
        | Mejorar los procesos de negociación mediante el uso del conocimiento.
      li 
        .lista-ol--cuadro__vineta
          span.text-dark.fw-bold 3
        | Reducir los tiempos de desarrollo de nuevos productos y mejorar los ya existentes.
      li 
        .lista-ol--cuadro__vineta
          span.text-dark.fw-bold 4
        | Acelerar la solución de problemas dentro de la organización. 
    
    p.mb-3 A pesar de que las grandes empresas suelen liderar en la implementación de costosos proyectos de gerencia del conocimiento, las pymes deben encontrar maneras más asequibles de gestionar este valioso recurso, adaptándose a sus limitaciones. Esto incluye la creación de una unidad de gerencia del conocimiento ajustada a las características específicas de la pyme.
    p.mb-4 Las pymes suelen enfrentar una serie de debilidades que limitan su capacidad de competir en igualdad de condiciones con las grandes empresas. Algunas de estas debilidades son:

    .row.align-items-center.mb-5
      .col-lg-5.d-none.d-lg-block(data-aos="fade-right")
        img(src='@/assets/curso/temas/t2/img8.png', alt='')
      .col-lg-7(data-aos="fade-left")
        .bg6.p-1
          SlyderA.t4.p-4.px-5.py-5
            .tarjeta.tarjeta--blanca.p-4
              .row.align-items-center.justify-content-evenly
                .col-lg-2.col-md-3.col-5.mb-3.mb-md-0
                  img(src='@/assets/curso/temas/t2/slyder1.svg', alt='')
                .col-lg-8.col-md-9 
                  h4 Debilidad tecnológica
                  p.mb-0 La falta de actualización tecnológica en la directiva y el personal técnico afecta la competitividad de la empresa.
            .tarjeta.tarjeta--blanca.p-4
              .row.align-items-center.justify-content-evenly                
                .col-lg-2.col-md-3.col-5.mb-3.mb-md-0
                  img(src='@/assets/curso/temas/t2/slyder2.svg', alt='')
                .col-lg-8.col-md-9 
                  h4 Debilidad financiera
                  p.mb-0 La falta de fortaleza financiera impide a las pymes acceder a ciertos mercados o responder adecuadamente a contingencias.            
            .tarjeta.tarjeta--blanca.p-4
              .row.align-items-center.justify-content-evenly    
                .col-lg-2.col-md-3.col-5.mb-3.mb-md-0
                  img(src='@/assets/curso/temas/t2/slyder3.svg', alt='')
                .col-lg-8.col-md-9 
                  h4 Debilidad gerencial
                  p.mb-0 La directiva carece de formación adecuada en gestión, lo que lleva a una dirección improvisada y a corto plazo.            
            .tarjeta.tarjeta--blanca.p-4
              .row.align-items-center.justify-content-evenly    
                .col-lg-2.col-md-3.col-5.mb-3.mb-md-0
                  img(src='@/assets/curso/temas/t2/slyder4.svg', alt='')
                .col-lg-8.col-md-9 
                  h4 Debilidad en mercadeo y ventas
                  p.mb-0 La falta de un equipo especializado en ventas dificulta la comercialización efectiva de los productos o servicios.            
            .tarjeta.tarjeta--blanca.p-4
              .row.align-items-center.justify-content-evenly    
                .col-lg-2.col-md-3.col-5.mb-3.mb-md-0
                  img(src='@/assets/curso/temas/t2/slyder5.svg', alt='')
                .col-lg-8.col-md-9 
                  h4 Estructuras organizativas informales
                  p.mb-0 Esto conlleva dificultades para alcanzar altos niveles de eficiencia y rentabilidad, afectando el crecimiento y éxito empresarial. 
    
    p.mb-4 Así mismo, se identifican una serie de condiciones para que la unidad de gerencia del conocimiento sea efectiva en una pyme, debe cumplir con ciertas condiciones específicas:

    .row
      .col-lg-4.d-none.d-lg-block(data-aos="fade-right")
        img(src='@/assets/curso/temas/t2/img9.png', alt='')
      .col-lg-8(data-aos="fade-left")
        AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row.justify-content-end(titulo="Económica")
            .col-lg-11.ps-lg-0
              p Debe operar con los menores recursos posibles, ya que las pymes no cuentan con el mismo poder económico que las grandes empresas.
          .row.justify-content-end(titulo="Simple")
            .col-lg-11.ps-lg-0
              p La estructura y los procesos de la unidad deben ser sencillos, ajustándose a la naturaleza de la pyme, sin comprometer la eficacia y eficiencia.
          .row.justify-content-end(titulo="Flexible")
            .col-lg-11.ps-lg-0
              p La unidad debe adaptarse a los cambios internos de la pyme, ya que estas empresas suelen modificar su funcionamiento según las circunstancias del mercado.
          .row.justify-content-end(titulo="Dinámica")
            .col-lg-11.ps-lg-0
              p La unidad debe ser tan dinámica como la propia pyme, permitiendo ajustes rápidos en respuesta a los desafíos.
          .row.justify-content-end(titulo="Maleable")
            .col-lg-11.ps-lg-0
              p La unidad debe expandirse o contraerse en función del crecimiento o contracción de la empresa, manteniendo siempre la calidad en la gestión del conocimiento. 
    

    Separador
    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 2.2  Finalidad de la información
    
    p.mb-4 Una pregunta frecuente entre los propietarios de pequeños negocios es:

    .row.justify-content-center.mb-4(data-aos="fade-down")
      .col-lg-10
        .cajon.color-acento-contenido.bg2.p-4
          p.mb-0 "¿Por qué debería gastar tiempo en registrar y calcular cifras que, aparentemente, no me servirán de nada, salvo para determinar los impuestos que debo pagar, cuando tengo tantas otras tareas urgentes que requieren mi atención?" 
    
    p.mb-4 Esta preocupación es comprensible, dado que las exigencias legales aumentan la carga administrativa, tanto en términos de registro de datos como de archivado. Sin embargo, es un error subestimar el valor de esta información, considerándola como una pérdida de tiempo o un gasto innecesario de recursos. Los registros contables además de que permiten cumplir con las obligaciones fiscales también proporcionan datos esenciales para comprender y gestionar mejor el negocio. Estos datos permiten conocer:

    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-sm-6.col-lg-4.mb-4.mb-lg-0
        .tarjeta--boton.color-acento-contenido.p-4.h-100.bg3.t3
          .row.justify-content-center.mb-3
            .col-5.img-deco
              img(src='@/assets/curso/temas/t2/boton1.png', alt='')
          h4.text-center La situación patrimonial y financiera
          p.text-center.mb-0 Ofrecen una visión clara del estado de la empresa en un período determinado.
      .col-sm-6.col-lg-4.mb-4.mb-lg-0
        .tarjeta--boton.color-acento-contenido.p-4.h-100.bg3.t3
          .row.justify-content-center.mb-3
            .col-5.img-deco
              img(src='@/assets/curso/temas/t2/boton2.png', alt='')
          h4.text-center La evolución del negocio
          p.text-center.mb-0 Proporcionan información actualizada que facilita la toma de decisiones más efectivas y racionales.
      .col-sm-6.col-lg-4.mb-4.mb-lg-0
        .tarjeta--boton.color-acento-contenido.p-4.h-100.bg3.t3
          .row.justify-content-center.mb-3
            .col-5.img-deco
              img(src='@/assets/curso/temas/t2/boton3.png', alt='')
          h4.text-center Las tendencias de las principales variables
          p.text-center.mb-0 Ayudan a identificar la forma en que los recursos propios están siendo utilizados y explotados. 

    p.mb-4 Para que el sistema contable sea verdaderamente útil y eficiente, debe cumplir con ciertos requisitos:

    .tarjeta--container.row.mb-3(data-aos="fade-down")
      .col-md.tarjeta.bg3.p-4
        .row.align-items-center
          .col-3.col-lg-5
            img.mb-2.px-lg-3(src='@/assets/curso/temas/t2/img13.png', alt='')
          .col-9.col-lg-7
            h5.bgtitulo1.mb-3.py-1.ps-2.pe-2 1. Ajustarse a las necesidades de la empresa
            p.mb-0.text-white El sistema debe proporcionar la información necesaria para la toma de decisiones empresariales.
      .col-md.tarjeta.bg4.p-4.p-lg-5
        .row.align-items-center
          .col-3.col-lg-5
            img.mb-2.px-lg-3(src='@/assets/curso/temas/t2/img14.png', alt='')
          .col-8.col-lg-7
            h5.bgtitulo1.mb-3.py-1.ps-2.pe-2 2. Ser manejable
            p.mb-0.text-white Los datos deben poder gestionarse sin complicaciones, incluso en entornos pequeños.
    
    .tarjeta--container.row.mb-5(data-aos="fade-down") 
      .col-md.tarjeta.bg3.p-4.p-lg-5
        .row.align-items-center
          .col-3.col-lg-5
            img.mb-2.px-lg-3(src='@/assets/curso/temas/t2/img15.png', alt='')
          .col-8.col-lg-7
            h5.bgtitulo1.mb-3.py-1.ps-2.pe-2 3. Fácil de interpretar
            p.mb-0.text-white Es fundamental que la información sea clara y comprensible para quienes la utilicen. 
      .col-md.tarjeta.bg4.p-4.p-lg-5
        .row.align-items-center
          .col-3.col-lg-5
            img.mb-2.px-lg-3(src='@/assets/curso/temas/t2/img16.png', alt='')
          .col-8.col-lg-7
            h5.bgtitulo1.mb-3.py-1.ps-2.pe-2 4. Reflejar con exactitud la realidad
